import React, { memo, useEffect, useState } from "react";
import { csv } from "d3-fetch";
import { scaleLinear } from "d3-scale";
import {
  ZoomableGroup,
  ComposableMap,
  Geographies,
  Geography,
  Sphere,
  Graticule
} from "react-simple-maps";


const geoUrl =
  "https://raw.githubusercontent.com/FireCubeStudios/colorrangedata/master/world-110m.json";
        // @ts-ignore
const rounded = num => {
  if (num > 1000000000) {
    return Math.round(num / 100000000) / 10 + "Bn";
  } else if (num > 1000000) {
    return Math.round(num / 100000) / 10 + "M";
  } else {
    return Math.round(num / 100) / 10 + "K";
  }
};
const colorScale = scaleLinear()
  .domain([0.29, 0.68])
  // @ts-ignore
  .range(["#f7f7f7", "#5ca7e0"]);
// @ts-ignore
const MapChart = ({ setTooltipContent, setTooltipText, setTooltipContentText }) => {
  const [data, setData] = useState([]);
  useEffect(() => {
    csv(`https://raw.githubusercontent.com/FireCubeStudios/colorrangedata/master/ColorRange.csv`).then((data) => {
        // @ts-ignore
      setData(data);
    });
  }, []);

  return (
    <>
      <ComposableMap data-tip=""projectionConfig={{ scale: 147}} >
  
      <Graticule stroke="#f7f7f7" strokeWidth={0.5} />
      <Sphere stroke="#f7f7f7" strokeWidth={0.5} />
        {data.length > 0 && (
          <Geographies geography={geoUrl}>
            {({ geographies }) =>
            geographies.map((geo) => {
                // @ts-ignore
              const d = data.find((s) => s.ISO3 === geo.properties.ISO_A3);
              return (
                <Geography
                        // @ts-ignore
                fill={d ? colorScale(d["color"]) : "#f7f7f7"}
                onClick={() => {
                  const { NAME, MINORITIES } = geo.properties;
                  setTooltipContent();
                //  setTooltipText(`${NAME} — ${rounded(MINORITIES)}`);
                setTooltipText(`${NAME}`);
                setTooltipContentText(`${MINORITIES}`);
                }}
                key={geo.rsmKey}
                geography={geo}
                onMouseDown={() => {
                  const { NAME, MINORITIES } = geo.properties;
                  setTooltipContent();
                 // setTooltipText(`${NAME} — ${rounded(MINORITIES)}`);
                 setTooltipText(`${NAME}`);
                 setTooltipContentText(`${MINORITIES}`);
                }}
                onMouseLeave={() => {
                }}
                style={{
                    default: {
                        outline: "#f7f7f7",
                        outlineWidth: "1.5"
                      },
                  hover: {
                    fill: "#45c443",
                    outline: "none"
                  },
                  pressed: {
                    fill: "#45c443",
                    outline: "none"
                  }
                }}
              />
              );
            })
          }
          </Geographies>
             )}

      </ComposableMap>
    </>
  );
};



export default memo(MapChart);
