import React, { Component } from 'react'
import { Accordion, Icon } from 'semantic-ui-react'
import { DefaultButton, PrimaryButton, Stack, IStackTokens } from 'office-ui-fabric-react';

export default class AccordionExampleStandard extends Component {
  state = { activeIndex: 0 }

  handleClick = (e, titleProps) => {
    const { index } = titleProps
    const { activeIndex } = this.state
    const newIndex = activeIndex === index ? -1 : index

    this.setState({ activeIndex: newIndex })
  }

  render() {
    const { activeIndex } = this.state

    return (
      <Accordion fluid styled inverted style={{backgroundColor: "#121212"}}>
      <Accordion.Title
        active={activeIndex === 0}
        index={0}
        onClick={this.handleClick}
      >
        <Icon name='dropdown' />
        What is discrimination?
      </Accordion.Title>
      <Accordion.Content active={activeIndex === 0}>
        <p>
        Ethnic discrimination is when someone is judged or discriminated against based on their background, color and ethnicity. 
        </p>
      </Accordion.Content>

      <Accordion.Title
        active={activeIndex === 1}
        index={1}
        onClick={this.handleClick}
      >
        <Icon name='dropdown' />
      What are ethnic minorities?
      </Accordion.Title>
      <Accordion.Content active={activeIndex === 1}>
        <p>
        Ethnic minorities are a group of whoms belief and culture differs from the majority especially if they have migrated to a new country which is different from their country of origin.  
        </p>
      </Accordion.Content>

      <Accordion.Title
        active={activeIndex === 2}
        index={2}
        onClick={this.handleClick}
      >
        <Icon name='dropdown' />
       Where does discrimination happen?
      </Accordion.Title>
      <Accordion.Content active={activeIndex === 2}>
        <p>
       Discrimination happens in every country. It can take many forms such as wokplace discirmination, racism, police brutality and more.
        </p>
      </Accordion.Content>
    </Accordion>
    )
  }
}
